.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
  z-index: 999;
  transition: all .5s linear 0s;
}

.header.active {
  background: #ffffff;
  box-shadow: 5px 5px 15px 0 rgba(31, 31, 31, 0.07);
  top: -100px;
}

.header.scroll {
  top: 0;
}

.header.app-function .header__nav ul li, .header.app-function .header__nav ul a {
  color: #131c2f;
}

.header.app-function .header__btn .btn-secondary {
  color: #131c2f;
}

.header .menu-open {
  position: absolute;
  left: 20px;
  top: 55%;
  width: 30px;
  height: 30px;
  display: none;
  z-index: 99;
}

@media only screen and (max-width: 767px) {
  .header .menu-open {
    display: block;
    transform: translate(0, -50%);
  }
}

.header .menu-open span {
  width: 30px;
  height: 2px;
  margin-top: 10px;
  background: #ec135c;
  display: block;
}

.header .menu-open span:before, .header .menu-open span:after {
  content: '';
  width: 30px;
  height: 2px;
  background: #ec135c;
  position: absolute;
  left: 0;
}

.header .menu-open span:before {
  top: 0;
}

.header .menu-open span:after {
  bottom: 8px;
}

.header__logo img {
  height: 40px;
}

.header__nav {
  display: flex;
  width: 100%;
