  font-size: 10px;
  position: relative;
  right: -10px;
  transition: all 0.3s;
}

.header__nav ul li.active i {
  transform: rotate(180deg);
}

.header__nav ul li:hover .header__nav__dropdown {
  opacity: 1;
  visibility: visible;
}

.header__nav__dropdown {
  width: 365px;
  position: absolute;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  background: #ffffff;
  right: -253px;
  top: 25px;
  border-radius: 10px;
  flex-direction: column;
  transition: all 0.3s;
  opacity: 0;
  visibility: hidden;
}

.header__nav__dropdown--icon {
  width: 445px;
  right: -308px;
}

.header__nav__dropdown:after {
  content: '';
  width: 100%;
  height: 10px;
  position: absolute;
  top: -10px;
}

.header__nav__dropdown:before {
  content: '';
  border: 8px solid transparent;
  border-bottom: 8px solid #ffffff;
  position: absolute;
  top: -16px;
  left: 30%;
}

.header__nav__dropdown__img {
  width: 55px;
}

.header__nav__dropdown__img img {
  height: 21px;
  margin-right: 30px;
}

.header__nav__dropdown__list {
  flex: 1 0;
}

.header__nav__dropdown li {
  cursor: pointer;
}

.header__nav__dropdown li:first-child a {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.header__nav__dropdown li:last-child a {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom: none;
}

.header__nav__dropdown a {
  padding: 23px 35px;
  display: flex;
